<template>
    <div id="DataMapContent">
        <el-card class="box-card" id="DataMapCard" shadow="always">

            <!--卡片标题-->
            <div slot="header" class="clearfix"><span>创建模板变量</span></div>

            <!--            <br>-->
            <!--            <el-row :gutter="20" class="elRowStype">-->
            <!--                <el-col :span="24">已有变量：</el-col>-->
            <!--            </el-row>-->

            <!--Table表格-->
            <el-table
                :data="tableData"
                style="width: 100%"
                border="true"
                stripe="true"
                :header-cell-style="{background:'#2f2f35',color:'#fff'}"
            >

                <el-table-column prop="key" label="Key（自定义变量名，在消息模板中使用）"></el-table-column>
                <el-table-column prop="value" label="Value（Json索引，赋值给左侧的变量）"></el-table-column>
                <el-table-column fixed="right" label="操作" width="100">
                    <template slot-scope="scope">
                        <el-button @click.native.prevent="deleteRow(scope.$index, tableData)" type="text" size="small">
                            移除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!-- 一个空行 -->
            <br><br><br>
            <!--            <el-row :gutter="20" class="elRowStype">-->
            <!--                <el-col :span="24">新增变量：</el-col>-->
            <!--            </el-row>-->

            <!--输入框，是从最初的（可增加数量的输入框）转变过来的-->
            <div v-for="(list, index) in mapList2" :key="index">
                <!-- 填写服务器地址，col之间的间隔为20 -->
                <el-row :gutter="20">

                    <!--Key-->
                    <el-col :span="12">
                        <div class="grid-content">
                            <el-input placeholder="自定义字符串，可以大小写，不能有符号" v-model="list.mapKey">
                                <template slot="prepend">
                                    Key:
                                </template>
                            </el-input>
                        </div>
                    </el-col>

                    <!--Value-->
                    <el-col :span="12">
                        <div class="grid-content">
                            <el-input placeholder="alerts[0].labels.alertname" v-model="list.mapValue">
                                <template slot="prepend">
                                    Value:
                                </template>
                            </el-input>
                        </div>
                    </el-col>

                </el-row>

            </div>

            <!--这个框框只是在这里占位置的，没有任何实际作用，只为了撑起按钮和输入框之间的距离-->


            <!--添加新的输入框（+号按钮）-->
            <el-button type="primary" v-on:click="addTableData" size="mini">新增</el-button>

            <!--这个框框只是在这里占位置的，没有任何实际作用，只为了撑起按钮和输入框之间的距离-->
            <el-row :gutter="20">
                <el-col :span="14"></el-col>
            </el-row>

            <!--提交按钮-->
            <el-row :gutter="20">
                <el-col :span="4">
                    <div class="grid-content"></div>
                </el-col>

                <el-col :span="16">
                    <div class="grid-content">
                        <el-button type="info" round id="subButton" v-on:click="post_data">提交更新</el-button>
                    </div>
                </el-col>

                <el-col :span="4">
                    <div class="grid-content"></div>
                </el-col>
            </el-row>


            <!--卡片结束位置-->
        </el-card>
    </div>
</template>

<script>

export default {
    name: "DataMap",
    data() {
        return {
            mapList2: [
                {
                    mapKey: '',
                    mapValue: ''
                }
            ],
            tableData: [{
                key: '{{ alertName }} ',
                value: 'alerts[0].labels.alertname',
            }, {
                key: '{{ aaa }}',
                value: 'alerts[0].status',
            }, {
                key: '{{ bbb }}',
                value: 'alerts[0].annotations.description',
            }, {
                key: '{{ ccc }}',
                value: 'alerts[0].startsAt',
            }],
        };
    },
    computed: {},
    components: {
        // DataMapTable,
    },
    methods: {

        // 添加tableData的一行数据（和上面的添加输入框不是一回事）
        addTableData: function () {
            let mapKey = this.mapList2[0].mapKey;
            let mapValue = this.mapList2[0].mapValue;

            if (mapKey.length === 0 || mapValue.length === 0) {
                alert("输入框不能为空...")
            } else {
                let tableRow = {
                    key: "{{ " + mapKey + " }}",
                    value: mapValue
                };
                this.tableData.push(tableRow);
                this.mapList2[0].mapKey = "";
                this.mapList2[0].mapValue = "";
                console.log(this.tableData);
                for (let i = 0; i < this.tableData.length; i++) {
                    console.log(this.tableData[i].mapKey);
                }
            }
        },

        // 删除一行数据
        deleteRow(index, rows) {
            console.log(index, rows);
            rows.splice(index, 1);
        },

        // 提交数据
        post_data: function () {
            const dataList = [];
            for (let i = 0; i < this.tableData.length; i++) {
                let mapKey = this.tableData[i].key;
                let mapValue = this.tableData[i].value;

                let mapKey2 = mapKey.split(" ")[1]

                let newKeyValue = {};
                newKeyValue[mapKey2] = mapValue;
                dataList.push(newKeyValue);
            }

            // console.log(dataList);

            this.axios.post('/web/map/', {
                "key_value_list": dataList
            })
                .then(response => {
                    // alert(response.data);
                    console.log(response.data);
                })
                .catch(err => {
                    console.log(err);
                });
        },
        // 提交数据结束
    },

}
</script>

